/* styles.css */

/* Estilo general del contenedor de la televisión */
.tv-container {
    position: relative;
    width: 800px; /* Aumenta el tamaño del contenedor */
    height: 500px; /* Aumenta la altura del contenedor */
    background-color: #f6c1c1; /* Rosa similar al de Los Simpson */
    border: 15px solid #e1a8a8; /* Bordes rosa oscuro más gruesos */
    border-radius: 15px; /* Bordes redondeados más marcados */
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.6); /* Sombra más pronunciada para efecto 3D */
    padding: 30px; /* Espacio interno aumentado para los controles */
    margin: 0 auto; /* Centra el contenedor */
  }
  
  /* Estilo del marco del video */
  .tv-frame {
    position: absolute;
    top: 20px;
    left: 20px;
    width: calc(100% - 40px); /* Ajusta el ancho del marco */
    height: calc(100% - 80px); /* Ajusta la altura del marco */
    background: black; /* Color de fondo del marco del video */
    border-radius: 10px; /* Bordes redondeados del marco del video */
    overflow: hidden; /* Oculta el contenido fuera del marco */
  }
  
  /* Estilo del iframe del video */
  .video {
    width: 100%;
    height: 100%;
  }
  
  /* Estilo de los controles */
  .tv-controls {
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
    gap: 20px; /* Espacio entre los botones */
  }
  
  .tv-controls button {
    background-color: #005f6b; /* Azul petróleo para los botones */
    color: white;
    border: none;
    padding: 15px 30px; /* Botón más grande */
    border-radius: 8px; /* Bordes redondeados del botón */
    cursor: pointer;
    font-size: 18px; /* Texto más grande */
    transition: background-color 0.3s, transform 0.3s; /* Transiciones suaves */
  }
  
  .tv-controls button:hover {
    background-color: #003f4f; /* Azul petróleo oscuro al pasar el ratón */
    transform: scale(1.05); /* Efecto de aumento al pasar el ratón */
  }
  