/* App.css */

/* Estilo base para la aplicación */
.app {
  font-family: Arial, sans-serif;
  transition: background-color 0.3s, color 0.3s;
  padding: 20px;
}

/* Modo claro */
.app.light {
  background-color: #ffffff;
  color: #000000;
}

/* Modo oscuro */
.app.dark {
  background-color: #000000;
  color: #ffffff;
}

/* Estilo para el botón de cambio de tema */
.theme-toggle {
  background-color: #005f6b; /* Azul petróleo para el botón de cambio de tema */
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s, transform 0.3s;
}

.theme-toggle:hover {
  background-color: #003f4f; /* Azul petróleo oscuro al pasar el ratón */
  transform: scale(1.05); /* Efecto de aumento al pasar el ratón */
}
